@import "../../global.scss";


.menu{
    width: 300px;
    height: 100vh;
    background: linear-gradient(90deg, rgb(14, 12, 12) 2%, rgb(53, 146, 17) 100%);
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active{
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 40px;
        color: white;
        width: 60%;

        li{
            margin-bottom: 30px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }

            &:hover{
                font-weight: 700;
                color: rgb(4, 142, 167);
            }
        }
    }
}